<template>
  <div id="app">
    <div class="redirect" v-if="redirect">
      <span>
        Для начала, нам нужно получить токен -
        <br />Через 3 секунды мы
        перенаправим вас на страницу получения Twitch API токена.
      </span>
    </div>
    <div class="block" v-else>
      <a
        :href="token_auth"
        class="tokenButton"
      >Получить токен снова (если текущий просрочен или нужен новый)</a>
      <ncform
        form-name="generator"
        :form-schema="form"
        v-model="form.value"
        :is-dirty.sync="form.modded"
        @change="change()"
      ></ncform>
      <div class="linkBar" :class="{ linkReady }">
        <div class="link">
          <input readonly v-model="link" ref="copy" />
          <a href="#" @click.prevent="copy()">
            <span>Копировать</span>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const API =
    "https://id.twitch.tv/oauth2/authorize?response_type=token&redirect_uri=https://aneyo.github.io/points.js&scope=channel:read:redemptions&client_id=loh6hmf1odxlkqbgmv7b22lw4bl5rm",
  DEFAULT_LINK = "https://aneyo.github.io/points.js/widget",
  DEFAULT_LINK_MESSAGE =
    "Заполните нужные поля и на этом месте появится ссылка.";

export default {
  name: "App",
  components: {},
  data() {
    let token = new URLSearchParams(document.location.hash.slice(1)).get(
      "access_token"
    );

    if (!token) setTimeout(() => (document.location.href = API), 3000);

    return {
      redirect: !token,
      token_auth: API,
      linkReady: false,
      link: DEFAULT_LINK_MESSAGE,
      form: {
        type: "object",
        properties: {
          token: {
            type: "string",
            ui: {
              label: "Токен",
              placeholder: "Можно получить перейдя по ссылке сверху"
            },
            value: token,
            rules: {
              required: {
                value: true,
                errMsg: "Для работы виджета нужен токен доступа к Twitch API!"
              }
            }
          },
          channel: {
            type: "string",
            ui: {
              label: "Имя канала (логин)",
              placeholder: "maidhonoka"
            },
            rules: {
              required: {
                value: true,
                errMsg:
                  "Без логина мы не сможем слушать эвенты канала, которые связанны с наградами."
              }
            }
          },
          list: {
            type: "string",
            default:
              "https://gist.githubusercontent.com/aneyo/4566b18ed624ac7c2b28daaedc28c7dd/raw/points.txt",
            ui: {
              label: "Ссылка на список со звуками",
              placeholder:
                "https://gist.githubusercontent.com/aneyo/4566b18ed624ac7c2b28daaedc28c7dd/raw/points.txt",
              description:
                "Список может быть в формате JSON или просто в виде ссылок разделенными новой линией (\\n)"
            },
            rules: {
              required: {
                value: true,
                errMsg: "А откуда звуки то брать?"
              },
              url: true
            }
          },
          reward1_name: {
            type: "string",
            ui: {
              label: "Название награды для проигрывания случайного звука",
              placeholder: "Звук?"
            },
            rules: {
              required: true
            }
          },
          reward2_name: {
            type: "string",
            ui: {
              label:
                "Название награды для проигрывания указанного звука (Опционально)",
              placeholder: "Звук!",
              description:
                'Что-бы эта награда работала как надо, включите параметр награды "Пользователь должен ввести текст".'
            }
          },
          random_list: {
            type: "boolean",
            default: true,
            ui: {
              label: "Использовать случайный порядок звуков",
              description:
                "Порядок будет менятся после каждой перезагрузки виджета."
            },
            rules: {
              required: true
            }
          },
          misc: {
            type: "object",
            ui: { showLabel: false, legend: "Прочее" },
            properties: {
              sounds_volume: {
                type: "number",
                default: 0.8,
                ui: {
                  label: "Громкость звуков (от 0 до 1)"
                },
                rules: {
                  required: {
                    value: true,
                    errMsg:
                      "Громкость звуков может быть только в райное от 0 до 1!"
                  },
                  number: true,
                  minimum: 0,
                  maximum: 1
                }
              },
              show_rewards: {
                type: "boolean",
                default: false,
                ui: {
                  label: "Показывать ли активированные награды?",
                  description:
                    "В верхнем левом углу на короткое время будет показанна использованная награда (кроме звуков)."
                }
              },
              show_sounds: {
                type: "boolean",
                default: true,
                ui: {
                  label: "Показывать ли использованные звуки?",
                  description:
                    "Во время использование звука в нижнем правом углу будет показанно временное уведомление, которое содержит имя пользователя и номер звука."
                }
              },
              sounds_show_one: {
                type: "boolean",
                default: false,
                ui: {
                  label: "Показывать ли только один играющий звук за раз?",
                  description:
                    "Вместо списка будет показанно только одно уведомление."
                }
              }
            }
          }
        },
        value: {},
        modded: false,
        globalConfig: {
          scrollToFailField: {
            enabled: false
          }
        }
      },
      cache: {
        list: null,
        list_result: null,
        list_timeout: null
      }
    };
  },
  methods: {
    async change() {
      let listJSON = await this.resolveJSONFormat();

      this.linkReady =
        this.form.value.token &&
        this.form.value.channel &&
        this.form.value.reward1_name &&
        this.form.value.list &&
        this.form.value.misc.sounds_volume >= 0 &&
        this.form.value.misc.sounds_volume <= 1;

      if (this.linkReady) {
        let params = [
          `t=${encodeURI(this.form.value.token)}`,
          `c=${encodeURI(this.form.value.channel)}`,
          `r=${encodeURI(this.form.value.reward1_name)}`,
          `v=${encodeURI(this.form.value.misc.sounds_volume)}`
        ];

        if (this.form.value.list)
          params.push(
            `${listJSON ? "j" : "l"}=${encodeURI(this.form.value.list)}`
          );

        if (this.form.value.reward2_name)
          params.push(`e=${encodeURI(this.form.value.reward2_name)}`);

        if (!this.form.value.random_list) params.push("fixed");

        if (!this.form.value.misc.show_rewards) params.push("R=0");
        if (!this.form.value.misc.show_sounds) params.push("S=0");
        else if (this.form.value.misc.sounds_show_one) params.push("S=2");

        this.link = `${DEFAULT_LINK}#` + params.join("&");
      } else this.link = DEFAULT_LINK_MESSAGE;
    },
    copy() {
      this.$refs["copy"].select();
      this.$refs["copy"].setSelectionRange(0, 10000);
      document.execCommand("copy");
    },
    resolveJSONFormat() {
      if (this.cache.list && this.cache.list == this.form.value.list)
        return this.list_result;

      this.cache.list = this.form.value.list;
      if (this.cache.list_timeout) clearTimeout(this.cache.list_timeout);

      return new Promise(resolve => {
        this.cache.list_timeout = setTimeout(async () => {
          let res = await fetch(this.form.value.list);

          try {
            let list = await res.json();
            resolve((this.list_result = list instanceof Array));
          } catch (e) {
            resolve((this.list_result = false));
          }
        }, 1000);
      });
    }
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  color: #2c3e50;
  margin-top: 60px;
  display: flex;
  justify-content: center;
  margin-bottom: 70px;
}

.tokenButton {
  margin: 0 13px;
  text-decoration: none;
  font-weight: bold;
}

.block {
  max-width: 600px;
}

.linkBar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: calc(100vw - 1rem);
  background: #2a2a2a;
  display: flex;
}

.linkBar > .link {
  background-color: #141414;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #444444;
  height: 40px;
  box-sizing: border-box;
  color: #cfcfcf;
  display: inline-block;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  flex: 1;
  margin: 0.75rem 1rem;
  display: flex;
  overflow: hidden;

  opacity: 0.5;
}

.linkBar:not(.linkReady) * {
  user-select: none;
  cursor: not-allowed;
}
.linkBar.linkReady > .link {
  opacity: 1;
}

.link > input {
  flex: 1;
  height: 40px;
  border: none;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  box-sizing: border-box;
  -webkit-appearance: none;
  background: transparent;
  color: #cfcfcf;
}

.link > a {
  text-decoration: none;
  color: white;
  background: #4a7ac4;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.4rem;
}
</style>
